import React, { useState } from 'react'
import Nav from '../templates/header'
import Pagination from './pagination'
import axios from '../API.js'

export default class CollapsibleTable extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			rows: [
				this.createData('Perch MTurk Requester', 'Perch MTurk Requester', '23,156', '5', '7d ago', 'Preview', 'Click & Work',1),
				this.createData('James Billing', 'James Billing', '20,156', '10', '7d ago', 'Preview', 'Click & Work',1),
				this.createData('ThreesJS', 'ThreesJS', '22,156', '10', '7d ago', 'Preview', 'Click & Work',1),
				this.createData('Scoutt', 'Scoutt', '19,156', '0.15', '7d ago', 'Preview', 'Click & Work',1),
				this.createData('Scott Antipa', 'Scott Antipa', '15,156', '0.15', '7d ago', 'Preview', 'Click & Work',1),
				this.createData('Ice cream sandwich', 237, 9.0, 37, 4.3, 4.99,1),
				this.createData('Eclair', 262, 16.0, 24, 6.0, 3.79,1),
				this.createData('Cupcake', 305, 3.7, 67, 4.3, 2.5,1),
				this.createData('Gingerbread', 356, 16.0, 49, 3.9, 1.5,1),
				this.createData('Frozen yoghurt', 159, 6.0, 24, 4.0, 3.99,1),
				this.createData('Ice cream sandwich', 237, 9.0, 37, 4.3, 4.99,1),
				this.createData('Eclair', 262, 16.0, 24, 6.0, 3.79,1),
				this.createData('Cupcake', 305, 3.7, 67, 4.3, 2.5,1),
				this.createData('Gingerbread', 356, 16.0, 49, 3.9, 1.5,1),
				this.createData('Frozen yoghurt', 159, 6.0, 24, 4.0, 3.99,1),
				this.createData('Eclair', 262, 16.0, 24, 6.0, 3.79,1),
				this.createData('Frozen yoghurt', 159, 6.0, 24, 4.0, 3.99,1),
				this.createData('Eclair', 262, 16.0, 24, 6.0, 3.79,1),

			]

		}
	}
	createData = (name, title, hits, reward, created, action, actionbutton,requesterid) => {
		return { name, title, hits, reward, created, action, actionbutton,requesterid };
	}

	componentDidMount() {
		var rowss = this.state
		axios.get("/Hits").then(res => {
			var data = res.data.data
			for (var i in data) {
				rowss.rows.unshift(this.createData(data[i].RequesterName, data[i].Title, data[i].hits, data[i].Reward, data[i].created, 'Preview', 'Click & Work', data[i].requesterid))
			}
			this.setState({ rowss })
		})
	}

	render() {
		return (<>
			<Nav mainactive="hits" subactive="allhits" />

			<div className="p-4">

				<Pagination key={1} rows={this.state.rows} {...this.props} />

			</div>
		</>)

	}
}

